.lang-section {
  z-index: +9999999999;
}

i {
  font-family: var(--fa-style-family, "Font Awesome 6 Free") !important;
}

.lang-section .lang-menu {
  width: 29px;
  height: 28px;
  border-radius: 100%;
  object-fit: cover;
  /* box-shadow: 0px 1px 8px 4px #ffffffb8; */
  color: white;
  margin-right: 8px;
}

.lang-section .sl-nav li:hover .pad-lang {
  position: absolute;
  top: -32px;
  padding-right: 30px;
  padding-left: 65px;
  padding-top: 40px;
  left: 25px;
}

.lang-section .sl-nav {
  display: inline;
}

.lang-section .sl-nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
  position: relative;
  display: inline-block;
}

.lang-section .sl-nav li {
  cursor: pointer;
  /* padding-bottom: 10px; */
}

.lang-section .sl-nav li ul {
  display: none;
}

.lang-section .sl-nav li:hover ul {
  position: absolute;
  top: 29px;
  right: -15px;
  display: block;
  background: #fff;
  width: 120px;
  padding-top: 0px;
  z-index: 1;
  border-radius: 5px;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2);
}

.lang-section .sl-nav li:hover .triangle {
  position: absolute;
  top: 15px;
  right: -10px;
  z-index: 10;
  height: 14px;
  overflow: hidden;
  width: 30px;
  background: transparent;
}

.lang-section .sl-nav li:hover .triangle:after {
  content: "";
  display: block;
  z-index: 20;
  width: 15px;
  transform: rotate(45deg) translateY(0px) translatex(10px);
  height: 15px;
  background: #fff;
  border-radius: 2px 0px 0px 0px;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2);
}

.lang-section .sl-nav li ul li {
  position: relative;
  text-align: left;
  background: transparent;
  padding: 15px 15px;
  padding-right: 2px;
  padding-bottom: 0;
  z-index: 2;
  font-size: 15px;
  color: #3c3c3c;
}

.lang-section .sl-nav li ul li:last-of-type {
  padding-bottom: 15px;
}

.lang-section .sl-nav li ul li span {
  padding-left: 5px;
}

.lang-section .sl-nav li ul li span:hover {
  color: #61b4ee;
}

.lang-section .sl-nav li ul li span.active {
  color: #61b4ee;
  font-weight: 700;
}

.lang-section .sl-flag {
  display: inline-block;
  /* box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.4); */
  width: 18px;
  height: 18px;
  background: #e9244f;
  border-radius: 50%;
  position: relative;
  top: 2px;
  overflow: hidden;
}

.lang-section .flag-fr {
  /* background: url("https://icons.iconarchive.com/icons/wikipedia/flags/512/FR-France-Flag-icon.png"); */
  background-size: cover;
  background-position: center center;
}

.lang-section .flag-usa {
  background-size: cover;
  background-position: center center;
  /* background: url("data:image/png;base64,"); */
}

.lang-section .lang-text {
  font-size: 14px;
}







